function fetchColumData(e){return $.ajax({type:"GET",url:e.url+"/columns"})}function fetchPreviewData(e){return $.ajax({type:"GET",url:e.url+"/preview"})}var TableActions=require("../actions/TableActions");module.exports={getTableData:function(e){$.when(fetchColumData(e),fetchPreviewData(e)).then(function(t,a){TableActions.receivedTableData(e,t[0],a[0])})}};