var QueryActions=require("../actions/QueryActions"),_=require("lodash");module.exports={getQuery:function(e){$.ajax({type:"GET",url:"./api/queries/"+e,success:function(e){QueryActions.receivedQuery(e)}})},getAllQueries:function(){$.ajax({type:"GET",url:"./api/queries",success:function(e){QueryActions.receivedQueries(e)}})},getUserQueries:function(e){$.ajax({type:"GET",url:"./api/users/"+e+"/queries",success:function(e){QueryActions.receivedQueries(e)}})},createQuery:function(e){$.ajax({type:"POST",url:"./api/query/saved",data:e,success:function(u){var r=_.extend(e,{uuid:u});QueryActions.receivedQuery(r)}})},updateQuery:function(e,u){$.ajax({type:"PATCH",url:"./api/queries/"+e,data:u,success:function(u){QueryActions.receivedUpdatedQuery(e,u)}})},destroyQuery:function(e){$.ajax({type:"DELETE",url:"./api/queries/"+e,success:function(){QueryActions.receivedDestroyedQuery(e)}})}};