function validFqnPart(t){return _.isString(t)&&!_.isEmpty(t)}function getParts(t){return t&&_.isString(t)?t.split("."):null}function schema(t){var n=getParts(t);return n&&n.length?n[0]:null}function table(t){var n=getParts(t);return n&&n.length?n[1]:null}function toFqn(t,n){return[t,n].join(".")}var _=require("lodash");module.exports={validFqnPart:validFqnPart,schema:schema,table:table,toFqn:toFqn};